import {
	mapMutations
} from 'vuex';
export default {
	data() {
		return {
			img: '/static/missing-face.png',
			checked:true,
			songdan:'../../static/delivery/songdan2.png',
			jiedan:'../../static/delivery/jiedanset.png',
			daliveryData:2
		};
	},
	methods: {
		...mapMutations(['logout']),
		// 跳转路由
		navTo(url) {
			uni.navigateTo({
				url: `../delivery/${url}`
			});
		},
		onChange(){
			
		},
		sd(e){
			if(e==1){
				this.songdan = '../../static/delivery/songdan.png';
				this.jiedan = '../../static/delivery/jiedanset1.png';
				this.daliveryData = 1;
				uni.navigateTo({
					url:'deliverylist'
				})
			}else if(e==2){
				this.songdan = '../../static/delivery/songdan2.png';
				this.jiedan = '../../static/delivery/jiedanset.png';
				this.daliveryData = 2
			}
		}
	}
}
